* {
    //text-rendering: optimizeLegibility !important
    text-rendering: geometricPrecision;
    -webkit-font-smoothing: antialiased !important;
    -webkit-text-size-adjust: 100% !important;
    -moz-osx-font-smoothing: grayscale;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }

html,body {
    cursor: default;
    // background: #0d0221 //#f5f7fb

    background: #080742;
    background: -moz-linear-gradient(180deg, $primary-degraded 0%, $primary-degraded 80%, $secondary-degraded 100%);
    background: -webkit-linear-gradient(180deg, $primary-degraded 0%, $primary-degraded 80%, $secondary-degraded 100%);
    background: linear-gradient(180deg, $primary-degraded 0%, $primary-degraded 80%, $secondary-degraded 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=$primary-degraded,endColorstr=$secondary-degraded,GradientType=1);

    // background: rgb(86,46,98)
    // background: -moz-linear-gradient(315deg, rgba(73,21,88,1) 0%, rgba(22,30,63,1) 100%)
    // background: -webkit-linear-gradient(315deg, rgba(73,21,88,1) 0%, rgba(22,30,63,1) 100%)
    // background: linear-gradient(315deg, rgba(73,21,88,1) 0%, rgba(22,30,63,1) 100%)
    // filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#491558",endColorstr="#161e3f",GradientType=1)
    color: $primary-font-color !important;
    font-family: $primary-font;
    font-weight: 300;
    font-size: 16px !important;
    line-height: 2rem;
    text-align: left; }

#Up {
    height: 1px; }
