.page-post {
    .postInfo {
        text-align: center;
        margin: 2.5em 0;
        .title {
            font-size: 2em;
            font-weight: 700;
            line-height: 1.4em;
            color: $primary-color;
            font-family: $secondary-font; }

        .info {
            font-size: .7em;
            .icon-minus {
                margin: 0 .5em;
                color: lighten($primary-font-color,50%); } } }

    .hrShareAndDonation {
        margin-top: 4em;
        margin-bottom: 4em; }

    #postContent {
        nav {
            background: #262626;
            color: white;
            padding: 1em;
            -webkit-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            -moz-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            -webkit-border-radius: .3em;
            border-radius: .3em;
            margin: 1em 1em 2em 1em;

            .toc {
                font-size: 1.1em;
                font-weight: 500; }

            ul {
                padding-left: 1em;
                margin: 0;

                li {
                    margin: 0; } }
            // .h2
            //     padding-left: 0em

            // .h3
            //     padding-left: 1.5em

            // .h4
            //     padding-left: 3em

            // .h5
            //     padding-left: 4.5em

            // .h6
 }            //     padding-left: 6em

        h1, h2, h3, h4, h5, h6 {
            font-weight: 600;
            margin-block-start: 5rem;
            margin-block-end: 2.5rem; }

        p {
            display: block;
            margin-block-start: 1em;
            margin-block-end: 2.5em;
            margin-inline-start: 0px;
            margin-inline-end: 0px; }

        img {
            max-width: 100%;
            -webkit-border-radius: .75em;
            border-radius: .75em;
            -webkit-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            -moz-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            display: block;
            margin-left: auto;
            margin-right: auto;
 }            // width: 50%

        a {
            color: $primary-color; }

        li {
            margin-block-start: 1em;
            margin-block-end: 2.5em;
            margin-inline-start: 0px;
            margin-inline-end: 0px; }

        figure {
            margin: 0;
            text-align: center;

            figcaption {
                font-size: .75em;
                line-height: 1.8em;
                padding: 1em 1.5em 2em 1.5em; } }

        .key {
            font-weight:      600;
            background-color: #f7f7f7;
            border:           1px solid #ccc;
            color:            #333;
            font-size:        .8em;
            line-height:      1.4em;
            text-shadow:      0 1px 0 #fff;
            display:          inline-block;
            padding:          0.1em 0.6em;
            margin:           0 0.1em;
            white-space:      nowrap;

            -webkit-box-shadow: 0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            -moz-box-shadow:    0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            box-shadow:         0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;

            -webkit-border-radius: 3px;
            -moz-border-radius:    3px;
            border-radius:         3px; }

        .terminal {
            background: #262626;
            color: white;
            padding: 1em;
            -webkit-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            -moz-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
            -webkit-border-radius: .3em;
            border-radius: .3em; }

        .shareAndDonation {
            margin-bottom: 1em;
            padding: 0 1em;
            font-size: .9rem;
            text-align: center;

            .message {
                display: block; }

            img {
                display: inline-block;
                -webkit-box-shadow: none;
                -moz-box-shadow: none;
                box-shadow: none; }

            .row [class^="col"] {
                margin: 0; } } } }
