.page-index {
    text-align: center;
    padding-top: 2em;
    padding-bottom: 2em;

    .nameAndPhoto {
        .name {
            font-size: 5em;
            font-weight: 700;
            line-height: .7em;
            text-shadow: 0 1px 0 #ccc;
            text-shadow: 0.05em 0.05em 0.2em rgba(10,10,10,0.5);
            margin-block-start: 0;
            margin-block-end: 0;
            z-index: 1;
            letter-spacing: -7px;

            .firstName {
                margin-left: -10%;
                display: block; }

            .lastName {
                padding-left: 10%;
                display: block; } }

        .photo {
            width: 100%;
            max-width: 150px;
            z-index: 2;
            margin-top: -10em;

            animation: zoomInDown;
            animation-duration: 3s;

            -webkit-filter: drop-shadow(0px 0px 20px rgba(0,0,0,0.5));
            filter: drop-shadow(0px 0px 20px rgba(0,0,0,0.5)); } }

    p.hi {
        font-size: 1.7em;
        line-height: 2em;
        font-family: $secondary-font;
        margin-bottom: .5em;
        font-size: 300;
        .name {
            color: $primary-color;
            font-weight: 400; } }

    p.aboutMeInfo {
        padding-top: 1em; }

    p.aboutMe {
        a {
            color: $primary-color; } } }

.page-blog {
    .year, .categories, .tags, #categoryName {
        color: $secondary-color;
        font-weight: 400;
        font-family: $secondary-font;
        font-size: .9em;
        text-transform: uppercase; }

    ul {
        list-style: none;
        padding-inline-start: 0;
        margin-top: 0; }

    .allCategories {
        li {
            a {
                cursor: pointer;
                border-bottom: 0px solid #000; } } }

    .article {
        margin: 0 0 2em 0;
        .title {
            line-height: 1.6em;
            a {
                border-bottom: none;
                &:hover {
                    .icon-link {
                        color: $primary-color; } } } }
